/* Styles */
:root {
    font-family: Montserrat, sans-serif;
    font-size: 14px;
    line-height: 1.29em;
    font-weight: 400;
    letter-spacing: -0.02em;

    color: #3b3a3a;

    font-synthesis: none;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-text-size-adjust: 100%;
}

body {
    padding: 100px 20px 20px;
}

.header {
    height: 90px;
    width: 100%;
    top: 0;
    left: 0;
    position: fixed;
    background-color: #305793;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.5), 0 8px 10px 0 rgba(0, 0, 0, 0.3);
}

.input-search {
    height: 40px;
    width: 280px;
    border-radius: 5px;
    border: 0px solid grey;
    text-align: center;
    font-family: Montserrat, sans-serif;
    margin-right: 16px;
}

.btn-search {
    height: 40px;
    border-radius: 5px;
    border: 0px solid grey;
    padding: 0px 40px;
    font-family: Montserrat, sans-serif;
    font-weight: 700;
    text-transform: uppercase;
    color: #000000;
    letter-spacing: 0.05em;
    background-color: #c4f433;
}

.load-more-hidden {
    display: none;
}

.load-more {
    height: 40px;
    border-radius: 5px;
    border: 0px solid grey;
    padding: 0px 40px;
    font-family: Montserrat, sans-serif;
    font-weight: 700;
    text-transform: uppercase;
    margin: 20px auto 20px auto;
}

.gallery {
    display: flex;
    flex-direction: row;
    gap: 20px 20px;
    flex-wrap: wrap;
}

.gallery-item {
    width: calc((100% - 20px) / 2);
}

@media only screen and (min-width: 1024px) {
    .gallery-item {
        width: calc((100% - (20px * 2)) / 3);
    }
}

@media only screen and (min-width: 1400px) {
    .gallery-item {
        width: calc((100% - (20px * 3)) / 4);
    }
}

.gallery-item-image {
    width: 100%;
    height: 250px;
    object-fit: cover;
    object-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.gallery-item-stats {
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    padding: 16px 8px;
    background-color: #cacaca;
}

.item-stat p,
h6 {
    font-size: 14px;
}